var render = function render(){var _vm=this,_c=_vm._self._c;return _c('div',{staticClass:"bgs",style:({ backgroundImage: 'url(bg/bglon2.webp),url(bg/bglon1.webp)' })},[_c('b-row',{staticClass:"fh"},[_c('b-col',{attrs:{"cols":"2"}},[_c('LeftCom')],1),_c('b-col',{attrs:{"cols":"8"}},[_c('b-row',{staticClass:"bookview"},[_c('div',{staticClass:"book"},[(_vm.kkk > 0)?_c('div',{staticClass:"pages",attrs:{"id":"pages"}},[_c('div',{staticClass:"page",class:_vm.getCl(0),style:(_vm.getZindex(0)),on:{"click":function($event){return _vm.clicks(0)}}},[_c('div',{staticClass:"book",style:({ backgroundImage: 'url(bg/a.png), url(' + _vm.anh + ')' })})]),_c('div',{staticClass:"page2",class:_vm.getCl(1),style:(_vm.getZindex(1))},[_c('Modau')],1),_c('div',{staticClass:"page scolls",class:_vm.getCl(2),style:(_vm.getZindex(2)),on:{"click":function($event){return _vm.clicks(2)}}},[_c('div',{staticClass:"box-content"},[_c('h2',{staticStyle:{"text-align":"center"}},[_c('span',{},[_vm._v("Sự tích Hoa Tuyết - Truyện cổ Andersen")])])])]),_c('div',{staticClass:"page scolls",class:_vm.getCl(3),style:(_vm.getZindex(3)),on:{"click":function($event){return _vm.clicks(3)}}},[_c('div',{staticClass:"box-content"},[_c('h3',[_c('span',{},[_c('em',[_vm._v("Nàng công chúa tuyết Xnhedinca bị ép đám cưới cùng Gió Bắc nhưng người nàng thật sự yêu và muốn bên cạnh là Gió Nam - em trai của Gió Bắc. Trải qua bao khó khăn họ vẫn không được ở bên nhau vì nàng đã tan ra trong vòng tay ôm của Gió Nam.....")])])]),_vm._v(" Khi bà Chúa Tuyết sinh hạ được một cô con gái, bà phải suy nghĩ rất lâu rồi mới quyết định đặt tên con gái là Xnhedinca. Xnhedinca trắng trẻo, mái tóc cùng trắng, nằm trên chiếc giường trắng, phía dưới là những tấm vải đệm bằng mây trắng. Khi Xnhedinca vừa đến tuổi trưởng thành đã có mấy chàng trai đến cầu hôn. Người đến trước nhất là Mặt Trăng, nhưng Xnhedinca không ưng vì chẳng có cái trán hói, đêm đêm không chịu ngủ mà cứ lang thang trên bầu trời, còn ban ngày thì lại giấu mặt sau những đám mây. Người thứ hai đến xin cầu hôn là Tia Nắng, nhưng chàng cũng bị Xnhedinca từ chối. Chúa Ông giận lắm. Một hôm ông nghiêm khắc nói với con gái: "),_c('br'),_vm._v("– Nếu con không tự kiếm được chồng thì cha mẹ đặt đâu, con phải ngồi đấy. Chúa Ông nhắn tin cho Gió, kẻ thống lĩnh cả bầu trời xanh có đến bốn người con trai chưa đứa nào thành gia thất. Gió bè đáp chiếc xe trang hoàng lộng lẫy do những con tuấn mã phi cực nhanh tới ra mắt Chúa Ông. Xnhedinca được gả bán cho đứa con trai cả của Gió "),_c('br'),_vm._v("– đó là chàng Gió Bắc. Chúa bà lấy làm hạnh phúc chuẩn bị của hồi môn cho con gái, nào là chăn lông chim, gối bông tuyết mềm, những tấm vải trải giường bằng mây trắng, rồi hàng chuỗi hạt cườm vòng cổ bằng tuyết lấp lánh. Khi khách mời đến đông đủ, Xnhedinca hiện ra như một nàng công chúa. Bà con họ hàng rất hài lòng được đến dự ngày vui, ai cũng khen hai bạn trẻ thật xứng đôi, phải lứa. Chỉ riêng Xnhedinca là không hài lòng chút nào. Chả là đương lúc yến tiệc, chàng Gió Bắc bỗng hét toáng lên: “Nóng quá! Nóng quá!” khi chàng khẽ chạm vào cặp môi lạnh toát của mình vào cặp môi của nàng. "),_c('br'),_vm._v("– Con không thể yêu chàng được "),_c('br'),_vm._v("– Xnhedinca thở dài nói. Nàng nói nhỏ đến mức ngoài mẹ nàng ra chẳng ai có thể nghe được tiếng nàng. “Không lẽ con gái yêu cảu ta lại là đứa bất hạnh”. Trái tim người mẹ bỗng run lên trước một sự tiên đoán đáng sợ. Đúng lúc yến tiệc đang rôm rả thì chàng rể lên tiếng bảo em trai Gió Nam chơi một bản nhạc nhảy. Gió Nam đang ngồi ở mép một đám mây bèn rút trong vạt áo ra một ống sáo và bắt đầu thổi. Giai điệu nhẹ nhàng lan toả, cnốn hút Xnhedinca vào cuộc nhảy. Nàng lả lướt, quay người, đập đập gót giày vào nhau phát ra tiếng kêu lanh canh, trong lúc đó, chàng Gió Đông, người em chồng tinh nghịch cứ vỗ tay cười. Chỉ có chàng Gió Tây là mỗi lúc một thêm thất vọng, đau khổ rồi gục đầu vào vai cha thổn thức. Gió Cha kinh ngạc hỏi: ")])]),_c('div',{staticClass:"page scolls",class:_vm.getCl(4),style:(_vm.getZindex(4)),on:{"click":function($event){return _vm.clicks(4)}}},[_c('div',{staticClass:"box-content"},[_vm._v(" – Con trai của ta, ngày vui thế này, có sao con lại để rơi luỵ? Chàng Gió Tây nức nở: "),_c('br'),_vm._v("– Vì sao cha lại dạm hỏi nàng Xnhedinca cho anh lớn mà không phải là cho con? Vì sao nàng lại không thể là vợ của con? Lúc này chàng Gió Nam mới ngước cặp mắt bồ câu của mình nhình Xnhedinca và chàng đã bắt gặp cái ánh nhìn lên của nàng. Tiếng sáo nghe càng du dương hơn khi nó vang lên chỉ để dành cho một mình Xnhedinca thôi, còn Xnhedinca thì nhảy cũng chỉ để cho chàng Gió Nam. Chuyện gì sẽ xảy ra nếu chàng Gió Bắc ác độc và đầy ghen tuông kia bắt được? Bà Chúa Tuyết vô cùng đau khổ. "),_c('br'),_vm._v("– Con gái yêu ơi, hãy biết kiềm chế trái tim mình! "),_c('br'),_vm._v("– Bà Chúa khẽ van nài khi Xnhedinca quay tròn gần chỗ bà. Nhưng một trái tim đang thổn thức vì tình yêu thì kiềm chế làm sao được? Chả lẽ Xnhedinca lại có thể làm được cái điều mà ngay cả những kẻ ngu ngốc lẫn những đấng anh minh đều chịu bó tay sao? Có thể vì mải nói chuyện với Chúa Ông nên Gió Bắc không hay biết gì, nếu như Gió Tây không khích bác chàng bằng một tiếng cười ác độc: "),_c('br'),_vm._v("– Xnhedinca của anh sắp nở hoa nhờ những cái nhìn cháy bỏng của thằng út nhà ta đấy. Nghe nói vậy, Gió Bắc đấm mạnh xuống bàn, hét lên vì tức giận, mắng Gió Nam: "),_c('br'),_vm._v("– Hãy cất ngay ống sao đi, nếu không ta sẽ đập vỡ đấy! Tiếng sao chỉ còn lí nhí như tiếng chim rồi câm bặt. Xnhedinca lúng túng liếc nhìn cặp mắt bồ câu của Gió Nam như muốn dò hỏi: phải chăng đó chỉ là tình yêu thoáng chốc của chàng. Nàng nhớ lại cái giây phút Gió Bắc đứng phắt dậy và gầm lên: ")])]),_c('div',{staticClass:"page scolls",class:_vm.getCl(5),style:(_vm.getZindex(5)),on:{"click":function($event){return _vm.clicks(5)}}},[_c('div',{staticClass:"box-content"},[_vm._v("– Xnhedinca, em đừng quên rằng em là của anh, còn mi, thằng em kia, đừng quên rằng này không hề là của mi! Và bây giờ, Xnhedinca, em hãy nhảy để anh đệm sáo! Gió Bắc cho các ngón tay vào mồm và huýt the thé khiến mọi người ai nấy có cảm giác bị kiến bò sau lưng. "),_c('br'),_vm._v("– Nhảy đi! Nhảy đi! "),_c('br'),_vm._v("– chàng hét Xnhedinca. Còn nàng thì đang đắm đuối trước đôi tay giơ ra chào mời của chàng Gió Nam. Nàng toan quay đi song đôi chân nàng bị băng cứng lại rồi, và chúng đã cưỡng lại ý nàng. "),_c('br'),_vm._v("– Hãy nhảy đi! Nàng hãy nhảy vì ta! "),_c('br'),_vm._v("– Gió bắc hét to đến nỗi làm chuyển cả những cột nhà bằng tuyết, nhưng Xnhedinca vẫn không hề động lòng. "),_c('br'),_vm._v("– A ha! "),_c('br'),_vm._v("– Gió Bắc cuồng lên, chàng rút cái roi ở thắt lưng ra đoạn vung lên. ")])]),_c('div',{staticClass:"page scolls",class:_vm.getCl(6),style:(_vm.getZindex(6)),on:{"click":function($event){return _vm.clicks(6)}}},[_c('div',{staticClass:"box-content"},[_vm._v("– Này, chú em Gió Nam của ta, giờ thì ta không còn thương tiếc mảnh vườn táo khu vườn hồng nhà mi nữa nghe. Nội đêm nay ta sẽ tàn phá hết những khu vườn đó bằng chính hơi thở của ta; sáng mai, mi sẽ được bước lên những cành khô khốc và chỉ biết rơi những giọt lệ cay đắng mà thôi. Tình yêu đã mách bảo Xnhedinca cách cứu lấy mạng sống của người tình. Khi Gió Bắc chưa kịp gom không khí vào lồng ngực thì Xnhedinca đã thấy gương mặt của Gió Nam đen xạm đi; nàng lập tức tháo tung những chăn gối lông chim của mình ra, và thế là chỉ trong nháy mắt, những khu vườn của Gió Nam đã được phủ một lớp thảm như tuyết trắng. Những bông hồng và những trái táo không còn biết sợ hơi thở của gió lạnh nữa. Gió Bắc thất vọng, tính chuyện trả thù Xnhedinca. Chàng dùng roi quất nàng túi bụi, song nàng đã khôn khéo tránh được. Gió Bắc bèn ném roi đi và lao về phía Xnhedinca. "),_c('br'),_vm._v("– Thế là đám cưới đã tàn! "),_c('br'),_vm._v("– chàng Gió Bắc gầm lên "),_c('br'),_vm._v("– Ta sẽ đưa nàng về nhà và giấu nàng vào căn hầm tối tăm nhất. Hãy để cho chuột, bọ gặm nhấm thân xác nàng, hỡi người vợ bướng bỉnh của ta. Đến đây, tìn yêu lại mách bảo Gió Nam cách cứu lấy trái tim, mà đối với nàng là quý giá hơn tất cả mọi thứ trên đời. Cắp nàng vào ngực, Gió Nam biết giấu Xnhedinca đi đâu bây giờ? Chàng đành phải đặt nàng dưới gốc cây hoa hồng và dặn nàng hãy chờ đợi chàng quay lại sau khi chàng chiến thắng người anh trong trận đấu cực kỳ khó khăn này. "),_c('br'),_vm._v("– Trước hết hãy hôn em đa, hỡi người tình duy nhất của đời em, em sẽ chờ đợi chàng, cho dù suốt cả cuộc đời. Gió Nam ôm hôn Xnhedinca một cách êm ái và hôn thật lâu cho đến khi người nàng tan ra trong vòng tay ôm của chàng, cho đến khi nàng chỉ còn là những giọt sương rơi xuống đất và tan biến đi. "),_c('br'),_vm._v("– Nàng ở đâu? Nàng trốn đâu rồi? "),_c('br'),_vm._v("– Gió bắc lao vào người em trai ")])]),_c('div',{staticClass:"page scolls",class:_vm.getCl(7),style:(_vm.getZindex(7)),on:{"click":function($event){return _vm.clicks(7)}}},[_c('div',{staticClass:"box-content"},[_vm._v(" – Ta vừa trông thấy mi ôm hôn nàng như thế nào kia mà. "),_c('br'),_vm._v("– Ôi anh trai của em, hà cớ gì ta cứ thù hằn nhau mãi, "),_c('br'),_vm._v("– Gió Nam buồn rầu đáp "),_c('br'),_vm._v("– Bây giờ nàng nằm ở kia kìa, như thể giọt sương, như giọt nước mắt đã tan biến vào đất. "),_c('br'),_vm._v("– Ta không tin nàng và cả mi nữa "),_c('br'),_vm._v("– Gió Bắc nói, nghiến răng trèo trẹo "),_c('br'),_vm._v("– Để nàng không bao giờ còn đứng dậy được, ta sẽ dùng băng giá đè nàng xuống. Gió Nam lúc nào cũng cứ quẩn quanh bên khu vườn hồng và vườn táo của mình. Cứ vào quãng cuối Đông hoặc đầu Xuân, Xnhedinca lại thấy có chàng ở bên cạnh, nàng dùng hơi thở sưởi ấm lớp băng bề mặt rồi ngước lên nhìn sâu vào cặp mắt bồ câu của người tình. Và người đời cứ mỗi khi nhìn thấy một bông hoa trắng nhỏ xíu không hiểu sao lại mừng rơn và kể cho nhau nghe như kể về một sự kiện gì trọng đại lắm: "),_c('br'),_vm._v("– Thấy không, trong vườn, Hoa Tuyết đã nở! ")])]),_c('div',{staticClass:"page",class:_vm.getCl(8),style:(_vm.getZindex(8)),on:{"click":function($event){return _vm.de